import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Pagination,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  Tooltip,
} from "@mui/material";
import axios from "axios";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import { usePayoutByLinkContext } from "./PayoutByLinkProvider";
import { useLocation } from "react-router-dom";
import toastMessage from "../../utils/toastMessage";

const status = {
  1: "Pending",
  2: "Success",
  3: "Failed",
};

const statusColor = {
  1: "btn-warning",
  2: "btn-success",
  3: "btn-danger",
};

const PayoutByLinkExistingUser = () => {
  const today = new Date().toISOString().split("T")[0];
  const [openPopup, setOpenPopup] = useState(false);
  const { token } = usePayoutByLinkContext();

  const location = useLocation();

  const [filters, setFilters] = useState({
    startDate: "",
    endDate: "",
    status: "",
    search: "",
  });

  const [data, setData] = useState([]);
  const [page, setPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [totalData, setTotalData] = useState(0);
  const [loader, setLoader] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {};

  const handleReset = () => {
    setFilters({
      startDate: "",
      endDate: "",
      status: "",
      search: "",
    });
    setPage(1);
  };

  const handleChangePage = (event, value) => {
    setPage(value);
  };

  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(parseInt(e.target.value, 10));
    setPage(1);
  };

  const handleTokenCopy = async () => {
    let userUrl =
      window.location.origin + "/" + "payout-link-user/" + token?.token;
    try {
      await navigator.clipboard.writeText(userUrl);
      //   setCopySuccess(true);
      toastMessage(false, "Url Copied Successfully");
    } catch (err) {
      console.error("Failed to copy!", err);
    }
  };

  const totalPages = Math.ceil(totalData / rowsPerPage);

  return (
    <>
      <Box justifyContent="flex-end" display="flex" mb={2} columnGap={5}>
        <Button
          variant="contained"
          sx={{
            width: "200px",
            backgroundColor: "#6C757D",
            color: "#fff",
          }}
        >
          Make Settlement
        </Button>

        <Button
          variant="contained"
          sx={{
            width: "200px",
            backgroundColor: "#F8F9FA",
            color: "var(--bs-primary2)",
          }}
          onClick={() => setOpenPopup(true)}
        >
          Copy Link
        </Button>
      </Box>

      <div className="card">
        <div className="card-header">Filters</div>
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="startDate"
                  className="form-control"
                  value={filters.startDate}
                  onChange={handleChange}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="endDate"
                  className="form-control"
                  value={filters.endDate}
                  onChange={handleChange}
                  max={today}
                />
              </div>

              <div className="form-group col-md-4">
                <label>Status</label>
                <FormControl fullWidth size="small">
                  <Select
                    value={filters?.status || ""}
                    name="status"
                    onChange={handleChange}
                    displayEmpty
                    sx={{
                      fontSize: "14px",
                      "& .MuiOutlinedInput-notchedOutline": {
                        borderColor: "var(--bs-primary2)",
                      },
                      "&:hover .MuiOutlinedInput-notchedOutline": {
                        borderColor: "var(--bs-primary3)",
                      },
                      "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                        borderColor: "var(--bs-primary2)",
                      },
                    }}
                  >
                    <MenuItem value="" disabled>
                      Select Status
                    </MenuItem>
                    {Object.entries(status).map(([key, value]) => (
                      <MenuItem key={key} value={key}>
                        {value}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </div>

              <div className="form-group col-md-4 mt-3">
                <label>Mobile No</label>
                <input
                  type="text"
                  name="search"
                  className="form-control"
                  value={filters.search}
                  onChange={handleChange}
                  placeholder="Mobile No"
                />
              </div>

              <div className="form-group col-md-4 mt-3">
                <label>Account Number</label>
                <input
                  type="text"
                  name="search"
                  className="form-control"
                  value={filters.search}
                  onChange={handleChange}
                  placeholder="Account Number"
                />
              </div>

              <div className="form-group col-md-12 mt-3">
                <button
                  type="button"
                  className="btn mr-3"
                  onClick={handleSubmit}
                  style={{
                    backgroundColor: "#6C757D",
                    color: "#fff",
                    // border: "1px solid var(--bs-primary2)",
                    padding: "8px 20px",
                    borderRadius: "8px",
                    fontWeight: 600,
                  }}
                >
                  Search
                </button>

                <button
                  type="button"
                  className="btn"
                  onClick={handleReset}
                  style={{
                    backgroundColor: "#F8F9FA",
                    color: "var(--bs-primary2)",
                    // border: "1px solid var(--bs-primary2)",
                    padding: "8px 20px",
                    borderRadius: "8px",
                    fontWeight: 600,
                  }}
                >
                  Reset
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>

      <Box sx={{ my: 2 }}>
        <TableContainer component={Box}>
          <Table
            sx={{
              "& .MuiTableCell-root": {
                whiteSpace: "nowrap",
              },
            }}
          >
            <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
              <TableRow>
                {[
                  "S.No.",
                  "Date",
                  "Transaction Id",
                  "UTR Number",
                  "Bank Name",
                  "Account Number",
                  "Opening Balance",
                  "Charges",
                  "Amount",
                  "Closing Balance",
                  "Status",
                  "Enquiry",
                ].map((heading) => (
                  <TableCell key={heading}>
                    <strong>{heading}</strong>
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>

            <TableBody>
              {data?.length > 0 ? (
                data.map((item, index) => (
                  <TableRow key={index} hover>
                    <TableCell>
                      {index + 1 + (page - 1) * rowsPerPage}
                    </TableCell>
                    <TableCell>{item?.createdAt}</TableCell>
                    <TableCell>{item?.txn_id}</TableCell>
                    <TableCell>{item?.utr || "Not generated"}</TableCell>
                    <TableCell>{item?.bank_name}</TableCell>
                    <TableCell>{item?.account_number}</TableCell>
                    <TableCell>{item?.o_bal}</TableCell>
                    <TableCell>{item?.charge}</TableCell>
                    <TableCell>{item?.amount}</TableCell>
                    <TableCell>{item?.c_bal}</TableCell>
                    <TableCell>
                      <button
                        className={`btn ${statusColor[item.status]}`}
                        style={{ width: "130px" }}
                      >
                        {status[item?.status] || "unknown"}
                      </button>
                    </TableCell>
                    <TableCell>
                      <Button
                        variant="contained"
                        disabled={loader || item?.status !== 1}
                        sx={{
                          width: "150px",
                          backgroundColor: "var(--bs-primary1)",
                          color: "white",
                        }}
                      >
                        Enquiry
                      </Button>
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell colSpan={12} align="center">
                    No data found
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
      </Box>

      <Box
        display="flex"
        justifyContent="space-between"
        alignItems="center"
        mt={2}
        flexWrap="wrap"
        gap={2}
      >
        <Box display="flex" alignItems="center" gap={2}>
          <FormControl size="small" sx={{ width: "150px" }}>
            <InputLabel>Rows per page</InputLabel>
            <Select
              value={rowsPerPage}
              onChange={handleChangeRowsPerPage}
              label="Rows per page"
            >
              {[5, 10, 25, 50].map((num) => (
                <MenuItem key={num} value={num}>
                  {num}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <Typography variant="body2" sx={{ fontSize: "14px", color: "#555" }}>
            Total Entries: {totalData}
          </Typography>
        </Box>

        <Pagination
          count={totalPages}
          page={page}
          onChange={handleChangePage}
          shape="rounded"
          showFirstButton
          showLastButton
          sx={{
            "& .MuiPaginationItem-root": {
              color: "var(--bs-primary1)",
              borderRadius: "8px",
            },
            "& .MuiPaginationItem-root.Mui-selected": {
              backgroundColor: "var(--bs-primary1)",
              color: "#fff",
              fontWeight: "bold",
            },
          }}
        />
      </Box>

      {/* // Dialog // */}

      <Dialog
        open={openPopup}
        onClose={() => setOpenPopup(false)}
        PaperProps={{
          sx: {
            borderRadius: 4,
            py: 2,
            px: 3,
            backdropFilter: "blur(10px)",
            background: "white",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 500,
          },
        }}
      >
        <DialogTitle
          sx={{
            color: "var(--bs-primary1)",
            fontWeight: 600,
            textAlign: "center",
            fontSize: "1rem",
          }}
        >
          View Url
        </DialogTitle>

        <DialogContent>
          <Box sx={{ display: "flex", gap: 1, alignItems: "center", mt: 2 }}>
            <Typography
              sx={{
                fontSize: "12px",
                fontWeight: 500,
                letterSpacing: 1,
                userSelect: "none",
                p: 1.5,
                borderRadius: 1,
                bgcolor: "#f5f5f5",
                color: " var(--bs-nav)",
                flex: 1,
                overflowX: "auto",
                filter: "none",
                transition: "filter 0.3s",
                border: "1px solid #ccc",
                fontFamily: "monospace",
              }}
            >
              {window.location.origin +
                "/" +
                "payout-link-user/" +
                token?.token}
            </Typography>

            <Tooltip title="Copy Url">
              <IconButton onClick={handleTokenCopy}>
                <ContentCopyIcon />
              </IconButton>
            </Tooltip>
          </Box>

          <Box sx={{ marginTop: 3 }}></Box>
        </DialogContent>

        <DialogActions sx={{ justifyContent: "space-between", py: 2, px: 3 }}>
          <Button
            onClick={() => setOpenPopup(false)}
            variant="outlined"
            color="error"
            sx={{ borderRadius: 2, px: 4, width: "150px" }}
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default PayoutByLinkExistingUser;
